import React, { Component } from 'react';
import 'css/pc/pc_advertisement.scss'
class PCAdvertisement extends Component {
  render() {
    return (
      <div className="ad-box">
        <div className='ad-wrap'>
          <img src='https://p3.pstatp.com/origin/33a1000ed281e399df61' alt='' />
          <h2>4K</h2>
          <span className="ad-name"></span>
          <span className="ad"></span>
        </div>
      </div>
    );
  }
}

export default PCAdvertisement;


